import React, { Component } from "react";
import "./Profil.scss";
import imgCintic from "../../../images/home/a-propos/outil-cintic.jpg";
import About from "../../About/About";
import Button from "../../Button/Button";
import karina from "../../../images/home/a-propos/karina-photo.jpg";
import karinaColors from "../../../images/home/a-propos/karina-colors.jpg";
import maquetteParis from "../../../images/home/a-propos/maquette-paris.jpg";
import personnagePoon from "../../../images/home/a-propos/personnage-poon.jpg";


export default class Profil extends Component {
  render() {
    return (
      <React.Fragment>
        <section id="profil">
          <div className="container-main-2">
            <div className="content-about purple-color">
              <div className="bloc-profil">
                <img
                  src={imgCintic}
                  alt="profil Emilie"
                  className="img-profil"
                />
              </div>
              <div className="bloc-about bloc-txt-about">
                <h2 className="title-profil">Mon parcours</h2>
                <p className="txt-profil">
                Jai suivi plusieurs formations dans lensemble des mtiers lis au design print et numrique. Ayant ainsi cumul les expriences en entreprises  travers mes diffrents stages (exemples ? ), je me suis forme auprs de professionnels trs comptents. Ces expriences me permettent aujourdhui davoir des connaissances thoriques et une vision terrain de gestion de projet globale.
                Je collabore aujourdhui avec des agences de communication et des annonceurs (entreprises et associations de toutes tailles).
                </p>
              </div>
            </div>
          </div>
        </section>

        <About
          img="https://img.aws.la-croix.com/2014/12/03/1246810/Quels-sont-atouts-France_1_730_400.jpg"
          paddingTop="120px"
          paddingBottom="120px"
          sizeTitle="40px"
          sizeTxt="24px"
          title="Mes atouts"
          txt="Dynamique et crative, je gre vos projets de la phase stratgique et de conception  livraison du produit fini. Je vous accompagne au quotidien et cherche  vous apporter un maximum de valeur afin que vos outils ne soient pas uniquement des supports de promotion mais aussi dadhsion et de fidlisation.
          Jaccorde une grande place  la crativit  travers mon style graphique original.     
          En tant que chef de projet, jattache une grande importance aux qualits indispensables lies  la gestion de projet : rigueur et organisation. Toujours  la recherche de la meilleure solution, je madapte  vos ventuelles contraintes."
        />

        <section id="other-passion">
          <div className="container-main">
            <div className="content-other-passion">
              <div className="bloc-content">
                <p className="txt-other-passion">
                En dehors de mon mtier de graphiste, je suis passionne par lillustration, notamment la cration de personnages fictifs inspirs de lunivers HeroicFantasy, mais aussi de lart traditionnel (peinture, dessin, photo de mode).Retrouvez mon univers sur Instagram, o je partage mes crations.
                </p>
                <Button txt="En savoir plus" colorTxt="#fff" padding="18px 20px" width="250px" bgColor="#ffd600" margin="60px" txtSize="1.4em" fWeight="bold" lSpacing="2px"/>
              </div>
              <div className="bloc-content">
                <div className="grid-img">
                    <div className="img">
                        <img src={karina} />
                        <div className="layer">
                          <div className="content-layer">
                            <h2 className="title-content">Karina en mode 1980</h2>
                          </div>
                        </div>
                    </div>
                    <div className="img">
                    <img src={karinaColors} />
                    <div className="layer">
                          <div className="content-layer">
                            <h2 className="title-content">Karina</h2>
                          </div>
                        </div>
                    </div>
                    <div className="img">
                    <img src={maquetteParis} />
                    <div className="layer">
                          <div className="content-layer">
                            <h2 className="title-content">Karina</h2>
                          </div>
                        </div>
                    </div>
                    <div className="img">
                    <img src={personnagePoon} className="img-other-passion" />
                    <div className="layer">
                          <div className="content-layer">
                            <h2 className="title-content">Karina en mode 1980</h2>
                          </div>
                        </div>
                    </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}
