import React from 'react';
import emailjs from 'emailjs-com';
import './Contact.scss';
import { Link } from "react-router-dom";

import emilie from "../../images/contact/emilie.png";
import instragramIcon from "../../images/home/footer/icone-instagram.png";
import linkedinIcon from "../../images/home/footer/icone-linkeding.png";
import behanceIcon from "../../images/home/footer/icone-behance.png";

export default function Contact() {


    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('gmail', 'portfolio', e.target, 'user_9fPraNQB6BGMJSIazbua6')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset();
    }


    const serviceLists = [
        {
            name: "Web Design",
            label: "webDesign"
        },
        {
            name: "UX/UI",
            label: "uxUi"

        },
        {
            name: "Motion Design",
            label: "motionDesign"
        },
        {
            name: "Illustration",
            label: "illustration"
        },
        {
            name: "Print : Affiche/Logotype",
            label: "printAfficheLogotype"
        }
    ]

    return (
        <section id="contact" className="margin-top-section">
            <div className="grid-contact">
                <aside className="contact-left">
                    <img src={emilie} className="contact-picture" />
                </aside>
                <aside className="contact-right">
                    <h2 className="white center size-40-px">Dcouvrons vos projets</h2>
                    <form className="margin-top-form" onSubmit={sendEmail}>
                        <input type="text" name="name" placeholder="Nom et prnom obligatoire*" className="input-style" />
                        <input type="email" name="email" placeholder="Email obligatoire*" className="input-style" />
                        <input type="tel" name="phone" placeholder="Numro de tlphone, non obligatoire" className="input-style" />
                        <label htmlFor="services" className="services-name">Quels services vous intressent ?</label>
                        <div className="grid-services">
                            {serviceLists.map((services, index) => {
                                return (
                                    <div className="services-lists" key={index}>
                                        <input type="checkbox" value={services.name} name="services" />
                                        <label htmlFor={services.label} className="label-services"> {services.name}</label>
                                    </div>
                                )
                            })}
                        </div>
                        <input type="text" name="subject" placeholder="Objet" className="input-style" />
                        <textarea name="message" id="textarea-contact" placeholder="Message" rows="10"></textarea>
                        <button className="btn-contact">Envoyer</button>

                        <div className="social-links">
                            <h2 className="white center mt-40px">Restons connecter</h2>
                            <div className="grid-social-links">
                                <span className="social-links-circles">
                                <div className="content-icon-contact icon-instagram">
                                <a href="https://www.instagram.com/lightningcreative/" target="_blank" rel="noopener">
                                <img
                                    src={instragramIcon}
                                    alt="instragam icon "
                                    className="icon-contact"
                                />
                                </a>
                                </div>
                                </span>
                                <span className="social-links-circles">
                                <div className="content-icon-contact icon-linkedin">
                                <a href="https://www.linkedin.com/in/emilie-pham-da-98173532/" target="_blank" rel="noopener">
                                <img
                                    src={linkedinIcon}
                                    alt="linkedin icon"
                                    className="icon-contact"
                                />
                                </a>
                            </div>
                                </span>
                                <span className="social-links-circles">
                                <div className="content-icon-contact icon-behance">
                                <a href="https://www.behance.net/emiliepham95e0" target="_blank" rel="noopener">
                                <img
                                    src={behanceIcon}
                                    alt="behance icon"
                                    className="icon-contact mt-15"
                                />
                                </a>
                            </div>
                                </span>
                                
                              
                            
                           
                            </div>
                        </div>
                    </form>
                </aside>
            </div>

        </section>
    )
}
